body {
  margin: 0;
  font-family: Merriweather, serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 15px;
  font-weight: 300;
  background-color: #E1E1D5;
  width:100vw;
}

.App-header {
  width:100vw;
  text-align: center;
  box-shadow: 0rem 0rem 1rem rgba(0,0,0,0.125);
  margin-bottom: 1rem;
  z-index: 2;
  background-color:white;
  padding:1rem 0rem 1rem 0;
  position:sticky;
  top:0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  display:inline-block;
  font-family: Oswald;
  font-weight: 500;
  background-color:black;
  color:white;
  font-size: 2.5rem;
  padding: 0.0625rem 0.375rem 0.0625rem 0.375rem;
  margin: 0 0 0.5rem 0;
}

h2 {
  font-family: Oswald;
  font-weight: 400;
  padding: 0;
  margin: 0;
  line-height: 1;
  font-size:2rem;
}

h3 {
  font-family: Oswald;
  font-weight: 500;
  padding: 0;
  font-size: 0.75rem;
  margin:0;
}

a {
  color: #28537C;
  text-decoration:none;
  font-weight: 400;
}

a:hover {
  text-decoration: underline;
  color:#3A7CB8;
}
